<template>
	<div class="enter" style="overflow:-Scroll;overflow-y:hidden;overflow-x:hidden">
		<!-- 视频背景 -->
		<div class="container" :style="contain">
			<video autoplay loop muted class="login-fillWidth" v-on:canplay="canplay">
				<source src="../../video/login.mp4" type="video/mp4" />
			</video>
		</div>
		<!-- 位置坐标动画 -->
		<div class="loc">
			<lottie-player src="https://assets5.lottiefiles.com/packages/lf20_osthueqs.json" background="transparent"
				speed="1" style="" loop autoplay>
			</lottie-player>
		</div>
		<div class="wrap clearfix">
			<!-- 查询表单 -->
			<el-form ref="EnterFormRef" :model="EnterForm" label-width="80px" class="enterForm" :rules="EnterFormRules"
				status-icon>
				<div class="font">
					<h1>录 入 患 者 信 息</h1>
				</div>
				<!-- 姓名输入 -->
				<div class="inputText">
					<el-form-item label="姓名" class="label" prop="name">
						<el-input v-model="EnterForm.name" prefix-icon="el-icon-user-solid"></el-input>
					</el-form-item>
				</div>
				<!-- 身份证号输入 -->
				<div class="inputText" style="margin-top: 40px;">
					<el-form-item label="身份证号" class="label" prop="identy">
						<el-input v-model="EnterForm.identy" prefix-icon="el-icon-postcard"></el-input>
					</el-form-item>
				</div>
				<!-- 日期 -->
				<div class="inputText" style="margin-top: 40px;">
					<el-form-item label="日期" class="label" prop="time">
						<el-col :span="11">
							<el-date-picker v-model="EnterForm.time" type="datetimerange"
								:picker-options="pickerOptions" range-separator="至" start-placeholder="开始日期"
								end-placeholder="结束日期" align="right" value-format="yyyy-MM-dd HH:mm:ss"
								format="yyyy-MM-dd HH:mm:ss">
							</el-date-picker>
						</el-col>
					</el-form-item>
				</div>
				<!-- 起始录入位置 -->
				<div class="inputText" style="margin-top: 40px;">
					<el-form-item label="起始位置" class="label" prop="start">
						<el-input v-model="EnterForm.start" prefix-icon="el-icon-location-information"></el-input>
					</el-form-item>
				</div>
				<!-- 终点录入位置 -->
				<div class="inputText" style="margin-top: 40px;">
					<el-form-item label="终止位置" class="label" prop="end">
						<el-input v-model="EnterForm.end" prefix-icon="el-icon-location-information"></el-input>
					</el-form-item>
				</div>
				<!-- 文件 -->
				<div class="inputText" style="margin-top: 40px;margin-left: 18%;width: 200px;">
					<el-upload class="upload-demo" ref="upload" action="" prop="fileList" :on-preview="handlePreview"
						:http-request="httpRequest" :on-remove="handleRemove" :file-list="EnterForm.fileList"
						:auto-upload="false">
						<el-button slot="trigger" size="small" type="primary" class="button3">选取文件</el-button>
						<el-button style="margin-left: 10px;" size="small" type="success" class="button4"
							@click="submitUpload()">上传文件
						</el-button>
						<div slot="tip" class="el-upload__tip" style="color: white;font-size: 12px;margin-left: -12%;">
							只能上传excel文件</div>
					</el-upload>
				</div>
				<!-- 按钮 -->
				<el-form-item>
					<el-button type="primary" round class="button1" @click="onload()">录 入</el-button>
					<el-button class="button2" round style="margin-left: 90px" @click="cancel()">
						取 消</el-button>
				</el-form-item>
			</el-form>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'Enter',
		data() {
			return {
				/* author:孙玉斐
				date:7.12
				elementui中的日期时间选择器的运行函数*/
				pickerOptions: {
					shortcuts: [{
						text: '最近一周',
						onClick(picker) {
							const enda = new Date();
							const starta = new Date();
							starta.setTime(starta.getTime() - 3600 * 1000 * 24 * 7);
							picker.$emit('pick', [starta, enda]);
						}
					}, {
						text: '最近一个月',
						onClick(picker) {
							const enda = new Date();
							const starta = new Date();
							starta.setTime(starta.getTime() - 3600 * 1000 * 24 * 30);
							picker.$emit('pick', [starta, enda]);
						}
					}, {
						text: '最近三个月',
						onClick(picker) {
							const enda = new Date();
							const starta = new Date();
							starta.setTime(starta.getTime() - 3600 * 1000 * 24 * 90);
							picker.$emit('pick', [starta, enda]);
						}
					}]
				},
				videoCanPlay: false,
				fixStyle: '',
				contain: {
					height: "",
					width: "",
				},
				/* author:孙玉斐
				date:7.12
				查询信息提交表单*/
				EnterForm: {
					name: '', //姓名
					identy: '', //身份证
					time: '', //时间
					start: '', //起始位置
					end: '', //终止位置
					fileList: [], //excel文件
				},

				EnterFormRules: {
					name: [{
							required: true,
							message: '请输入姓名',
							trigger: 'blur'
						},
						{
							pattern: '^[\\u4e00-\\u9fa5]{2,4}$',
							message: '请输入正确的姓名',
							trigger: 'blur'
						}
					],
					identy: [{
							required: true,
							message: "请输入身份证号",
							trigger: "blur"
						},
						{
							pattern: '^[1-9]\\d{5}(18|19|20|(3\\d))\\d{2}((0[1-9])|(1[0-2]))(([0-2][1-9])|10|20|30|31)\\d{3}[0-9Xx]$',
							message: '请输入正确的身份证号',
							trigger: 'blur'
						}
					],
					start: [{
						required: true,
						message: "请输入地址信息",
						trigger: "blur"
					}, ],
					end: [{
						required: true,
						message: "请输入地址信息",
						trigger: "blur"
					}, ],
				},
			}
		},
		methods: {
			/* author:孙玉斐 
			date:7.13
			将上传的物理地址转化为经纬度并且将人工录入信息表单上传至服务器*/
			onload() {
				this.$refs.EnterFormRef.validate(async valid => {
					if (!valid) {
						this.$refs.EnterFormRef.resetFields()
					}

					let formData = new FormData();
					for (let key in this.EnterForm) {
						formData.append(key, this.EnterForm[key]);
						console.log(formData.get(key));
					}

					this.$confirm('是否确认录入?', '提示', {
						confirmButtonText: '确定',
						cancelButtonText: '取消',
						type: 'warning'
					}).then(() => {
						this.$axios
							.post('https://120.25.76.71/api/entry', {
								"name": this.EnterForm.name,
								"identy": this.EnterForm.identy,
								"start": this.EnterForm.start,
								"end": this.EnterForm.end,
								"time": this.EnterForm.time,
							})
							.then(response => {
								this.$message.success('成功录入患者')
								this.$refs.EnterFormRef.resetFields();
							}, error => {
								console.log(error)
								console.log(this.EnterForm.time)
								this.$message.error('录入失败')
							})
					}).catch(() => {});
				});
			},

			/* author:孙玉斐
			date:7.13
			为取消按钮增加信息提示框*/
			cancel() {
				this.$confirm('是否确认要取消录入?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.$refs.EnterFormRef.resetFields()
				}).catch(() => {});
			},
			/* author:孙玉斐
			date:7.15
			视频显示函数，将控制视频显示参数置位true*/
			canplay() {
				this.videoCanPlay = true
			},
			/* author:孙玉斐
			date:7.15
			实现页面大小自适应*/
			getDefault() {
				this.contain.height = window.innerHeight - 62 + "px";
				this.contain.width = window.innerWidth - 201 + "px";
			},

			submitUpload() {
				this.$confirm('是否确认提交文件?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.$refs.upload.submit();
				}).catch(() => {});
			},

			// param是自带参数。 this.$refs.upload.submit() 会自动调用 httpRequest方法.在里面取得file
			httpRequest(param) {
				console.log(param)
				let fileObj = param.file // 相当于input里取得的files
				let fd = new FormData() // FormData 对象
				fd.append('file', fileObj) // 文件对象
				console.log(fd)
				let config = {
					headers: {
						'Content-Type': 'multipart/form-data'
					}
				}
				this.$axios
					.post('https://120.25.76.71/api/upload', fd, config)
					.then(response => {
						this.$message.success('成功录入患者')
						this.$refs.EnterFormRef.resetFields();
					}, error => {
						console.log(error)
						console.log(this.EnterForm.fileList)
						console.log(param.file)
						this.$message.error('录入失败')
					})
			},

			handleRemove(file, fileList) {
				console.log(file, fileList);
				this.$message.warning('当前限制选择 1 个文件，请删除后继续上传')
			},

			handlePreview(file) {
				console.log(file);
				console.log(file.raw.type)
				console.log(file.size)
				const isText = file.raw.type === 'application/vnd.ms-excel'
				const isTextComputer = file.raw.type ===
					'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
				const isLt2M = file.size / 1024 / 1024 < 2
				if (!isText && !isTextComputer) {
					this.$message.error('上传的文件只能是 Excel 文件!')
				}
				if (!isLt2M) {
					this.$message.error('上传文件大小不能超过 2MB!')
				}
				return isText && isTextComputer && isLt2M
			}
		},
		/* author:孙玉斐
		date:7.16
		页面创建时执行一次getDefault进行赋值，顺道绑定resize事件，实现页面大小的自适应*/
		created() {
			window.addEventListener("resize", this.getDefault);
			this.getDefault();
		},
	}
</script>

<style>
	.enter .container {
		position: absolute;
		z-index: -1;
		margin: 0;
		padding: 0;
	}

	.enter .container video {
		width: 100%;
		height: 100%;
		margin: 0;
		padding: 0;
	}

	.enterForm {
		margin-top: -3%;
	}

	.enter .wrap {
		margin-top: 5%;
		margin-left: 30%;
		opacity: 1;
		width: 600px;
		height: 700px;
		background-color: rgba(0, 0, 0, 0.2);
		color: white;
		border-radius: 20px;
		box-shadow: 0 0 10px white inset;
	}

	.label .el-form-item__label {
		color: #fff;
		font-size: 13px;
	}

	.enterForm .inputText {
		width: 480px;
		margin-left: 6%;
		margin-top: 24%;
	}

	.enterForm .button1 {
		margin-left: -12%;
		width: 90px;
		height: 45px;
		margin-top: 30px;
		font-size: 17px;
		border: none;
		color: #111;
		background-image: linear-gradient(to top, #fad0c4 0%, #ffd1ff 100%);
	}

	.enterForm .button2 {
		margin-left: -12%;
		width: 90px;
		height: 45px;
		margin-top: 30px;
		font-size: 17px;
		border: none;
		color: #111;
		background-image: linear-gradient(120deg, #e0c3fc 0%, #8ec5fc 100%);
	}

	.enterForm .font {
		margin-left: 34%;
		margin-top: 8%;
		font: bold 50px arial, sans-serif;
		font-family: youzai;
	}

	.loc lottie-player {
		width: 170px;
		height: 170px;
		position: absolute;
		right: 0;
		z-index: -1;
	}
</style>
