import Vue from 'vue'
import App from './App.vue'
import router from './router'
import ElementUI from 'element-ui'; // 导入 elementUI
import locale from 'element-ui/lib/locale/lang/en'// 完整引入 Element
import './plugins/element.js'
import 'element-ui/lib/theme-chalk/index.css'; // 导入 css
import './assets/css/global.css' // 导入全局样式表
import './assets/reset.css'
import './assets/font/font.css'
import 'swiper/dist/css/swiper.css'
import axios from 'axios'
import VideoBg from 'vue-videobg'//导入vue视频组件
import store from './store'
import VueAwesomeSwiper from 'vue-awesome-swiper'
import animated from 'animate.css'
import * as echarts from 'echarts'
	import vueLive2d from 'vue-live2d'

Vue.component('video-bg', VideoBg)
Vue.use(ElementUI, { locale })
Vue.use(VueAwesomeSwiper);
Vue.use(animated)
Vue.prototype.$axios = axios
Vue.prototype.$echarts = echarts
Vue.config.productionTip = false


axios.defaults.baseURL = 'https://www.zewest.cn:3000';

new Vue({
  axios,
  router,
	store,
  render: h => h(App)
}).$mount('#app')
