<template onload="initMap()">
	<!-- 定义地图显示容器 -->
	<div class="box">
		<!-- 流调按钮 -->
		<div id="container"></div>
		<div class="info1">
			<el-button type="primary" round class="button1" @click="getOtherLocation()">查 询</el-button>
		</div>
		<!-- 回溯按钮 -->
		<div class="info2">
			<el-button type="primary" round class="button2" @click="change()">回 溯</el-button>
		</div>
		<!-- 添加提示信息 -->
		<div class="word1">
			<div class="line1"></div>
			<span style="color: white;">红色的线表示重大患者本人路径</span>
		</div>
		<!-- 添加提示信息 -->
		<div class="word2">
			<div class="line2"></div>
			<span style="color: white;">绿色的线表示与患者有接触人员路径</span>
		</div>
		<!-- 添加提示信息 -->
		<div class="word3">
			<div class="circle1"></div>
			<span style="color: white;">红色标记点表示有重大患者经过的地区</span>
		</div>
		<!-- 添加提示信息 -->
		<div class="word4">
			<div class="circle2"></div>
			<span style="color: white;">蓝色标记点表示与患者有接触人员经过的地区</span>
		</div>
		<!-- 添加提示信息 -->
		<div class="word5">
			<div class="circle3"></div>
			<span style="color: white;">褐色标记点表示疑似患者经过的地区</span>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			name: 'Map'
			return {
				judge: true,
				marked: [], //存储服务器得来的经纬度数据
				path: [], //存储路径各点坐标
				tril: [], //临时变量来存储坐标最后赋值给path
				name: [], //存储姓名信息
				identy: [], //存储身份证信息
				Map, //地图图层
				markerLayer: "", //查询的人标记图层
				polylineLayer: "", //查询的人折线图层
				othermarkerLayer: "", //其他人的标记图层
				otherpolylineLayer: "", //其他人的折线图层
				temp: [],
				nowname: "",
				nowidenty: "",
				judge: true,
			}
		},
		methods: {
			initMap() {
				//将数据分类并添加styleId（数据为js文件引入）
				dotData.forEach(function(item, index) {
					if (index <= 358) {
						item.styleId = "redStyleColor";
						item.hierarchy = 4;
					} else if (index > 358 && index <= 2686) {
						item.styleId = "blueStyleColor";
						item.hierarchy = 3;
					}
					// else if (index > 2023 && index <= 2686) {
					// 	item.styleId = "blueStyleColor";
					// 	item.hierarchy = 2;
					// } 
					else {
						item.styleId = "yellowStyleColor";
						item.hierarchy = 1;
					}
				});

				let that = this;
				//初始化地图
				that.Map = new TMap.Map("container", {
					zoom: 4.4, //设置地图缩放级别
					center: new TMap.LatLng(36.32434341048188, 106.26656561894674), //设置地图中心点坐标
					mapStyleId: "style1", //个性化样式
					baseMap: {
						type: "vector",
						features: ["base", "building3d"], // 隐藏矢量文字
					},
				});

				//初始化散点图并添加至map图层
				var dot = new TMap.visualization.Dot({
						styles: {
							greenStyleColor: {
								type: "circle", //圆形样式
								fillColor: "#7FFF00", //填充颜色
								radius: 0.5, //原型半径
							},
							blueStyleColor: {
								type: "circle", //圆形样式
								fillColor: "#00BFFF", //填充颜色	
								radius: 1, //原型半径
							},
							yellowStyleColor: {
								type: "circle", //圆形样式
								fillColor: "rgba(204,114,24,0.9)", //填充颜色
								radius: 1, //原型半径
							},
							redStyleColor: {
								type: "circle", //圆形样式
								fillColor: "#FF0000", //填充颜色
								strokeWidth: 0, //边线宽度
								radius: 2.5, //原型半径
							},
						},
					})
					.addTo(that.Map)
				dot.setData(
					dotData.sort((a, b) => a.hierarchy - b.hierarchy) //显示层级排序
				); //设置数据
			},
			/*author:李浩杨
			  data:2021/7/16
			  实现地图上标记与患者相交路线的路径与区域*/
			getOtherLocation() {
				this.$confirm('是否确认要查询指定人员?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					let that = this
					that.othermarkerLayer.setVisible(true)
					that.otherpolylineLayer.setVisible(true)
					if (that.judge) {
						that.judge = !that.judge
						this.$axios
							.post('https://120.25.76.71/api/func', {
								"name": that.temp["name"],
								"identy": that.temp["identy"],
								"time": that.temp["time"]
							})
							.then(res => {
								for (var i = 2; i < res.data.all_routes.length; i += 3) {
									for (var j = 0; j < res.data.all_routes[i].length; j++) {
										var arr = {
											firlat: res.data.all_routes[i]. [j]. ["0"],
											firlng: res.data.all_routes[i]. [j]. ["1"],
											firTime: res.data.all_routes[i]. [j]. ["2"],
											laslat: res.data.all_routes[i]. [j]. ["3"],
											laslng: res.data.all_routes[i]. [j]. ["4"],
											lasTime: res.data.all_routes[i]. [j]. ["5"],
										}
										let oddlist = that.marked[res.data.all_routes. [i - 2]] || []
										oddlist.push(arr)
										that.marked[res.data.all_routes. [i - 2]] = oddlist
									}
									that.name.push(res.data.all_routes. [i - 2])
									that.identy.push(res.data.all_routes. [i - 1])
								}
								console.log(that.identy)
								console.log(that.marked)
								console.log(that.marked[that.name[0]])
								//将获取过来的数组进行for循环来为标记图层加标记
								for (var i = 0; i < that.name.length; i++) {
									var num = 0;
									for (var j = 0; j < that.marked[that.name[i]].length; j++) {
										that.othermarkerLayer.add({
											"id": that.name[i] + num,
											// "styleId":'otherStyle',
											"position": new TMap.LatLng(that.marked[that.name[i]]. [j]
												.firlat, that
												.marked[that.name[i]]. [j].firlng),
											//如果要在信息框显示人的名字，可以在这里加入自定义的属性讲name存入
											"properties": {
												"name": that.name[i],
												"Time": that.marked[that.name[i]]. [j].firTime,
												"identy": that.identy[i]
											}
										})
										num += 1
										that.othermarkerLayer.add({
											"id": that.name[i] + num,
											// "styleId":'otherStyle',
											"position": new TMap.LatLng(that.marked[that.name[i]]. [j]
												.laslat, that
												.marked[that.name[i]]. [j].laslng),
											"properties": {
												"name": that.name[i],
												"Time": that.marked[that.name[i]]. [j].lasTime,
												"identy": that.identy[i]
											}
										})
										num += 1
									}
								}
								//加折线
								for (var i = 0; i < that.name.length; i++) {
									var otherPolyArray = new Array();
									var z = 0
									for (var j = 0; j < that.marked[that.name[i]].length; j++) {
										otherPolyArray[z] = [that.marked[that.name[i]]. [j].firlat, that
											.marked[that.name[i]].
											[j].firlng
										]
										z += 1;
										otherPolyArray[z] = [that.marked[that.name[i]]. [j].laslat, that
											.marked[that.name[i]].
											[j].laslng
										]
										z += 1;
									}
									console.log(otherPolyArray)
									otherPolyArray = otherPolyArray.map(p => {
										return new TMap.LatLng(p[0], p[1]);
									})
									console.log(otherPolyArray)
									that.otherpolylineLayer.add({ //新的折线添加到图层中
										'id': that.name[i],
										'styleId': 'style_green',
										'paths': otherPolyArray
									})
								}
								//加轨迹
								//for(var i=0;i<that.name.length;i++){
								//  var otherpath = new Array()	
								// }
							});
					} else {
						that.othermarkerLayer.setVisible(false)
						that.otherpolylineLayer.setVisible(false)
						console.log("隐藏")
						that.judge = !that.judge
					}
				}).catch(() => {});
			},

			/*author:李浩杨、孙玉斐
			  data:2021/7/16
			  在页面显示地图并实现地图上标记和点标记显示信息窗口的功能和存储从服务器获得的值*/
			sign(x, y, z) {
				// 定义地图中心点坐标
				var center = new window.TMap.LatLng(39.792151, 116.397607)
				// 定义map变量，调用 TMap.Map() 构造函数创建地图
				this.Map = new window.TMap.Map(document.getElementById('container'), {
					center: center, // 设置地图中心点坐标
					zoom: 16, // 设置地图缩放级别
					pitch: 33.5, // 设置俯仰角
					rotation: 45, // 设置地图旋转角度
					mapStyleId: 'style1',
					baseMap: { //底图设置
						type: 'vector', //类型：失量底图
						features: ['base', 'building2d']
						//仅渲染：道路及底面(base) + 2d建筑物(building2d)，以达到隐藏文字的效果
					}
				})
				/*author:李浩杨
				  data:2021/7/12
				  实现地图上标记和点标记显示信息窗口的功能和存储从服务器获得的值*/
				let that = this;
				if (that.temp["time"] == null) {
					that.temp["time"] = this.$route.query.time;
					that.temp["name"] = this.$route.query.name;
					that.temp["identy"] = this.$route.query.identy
				}
				console.log(that.temp)
				this.$axios
					.post('https://120.25.76.71/api/func', {
						"name": x,
						"identy": y,
						"time": z
					})
					.then(res => {
						console.log(res.data);
						console.log(res.data.routes.length)
						for (var i = 0; i < res.data.routes.length; i++) {
							var array = {
								lat: res.data.routes[i]. ["1"],
								lng: res.data.routes[i]. ["2"],
								time: res.data.routes[i]. ["0"]
							};
							let oddlist = that.marked['location'] || []
							oddlist.push(array)
							that.marked['location'] = oddlist
							that.tril.push(array)
						}
						var j = 0
						for (var item in that.tril) {
							that.path[j] = [that.tril[j].lat, that.tril[j].lng],
								j = j + 1
						}
						//将地址定位到第一个点
						that.Map.setCenter(new TMap.LatLng(that.path. [0]. [0], that.path. [0]. [1]))
						/*author:李浩杨
						data:2021/7/13
						实现逆地址解析并将结果显示到信息窗口来显示,使用get请求来实现逆地址解析*/
						for (var i = 0; i < that.marked.location.length; i++) {
							// 遍历marked的数组来加点
							that.markerLayer.add({
								"id": i, //点标记唯一标识
								"styleId": 'myStyle',
								"position": new TMap.LatLng(that.marked.location[i]. ["lat"], that.marked
									.location[i]. ["lng"]),
								"properties": {
									"name": res.data.name,
									"Time": that.marked.location[i].time
								}
							}), error => {
								console.log("错误")
							}
						}
						//轨迹图
						var trail = new TMap.visualization.Trail({
								map: that.Map,
								pickStyle: function(item) { //轨迹图样式映射函数
									return {
										width: 10,
										color: "#00FFFF",
									}
								},
								startTime: 0, //动画循环周期的起始时间戳
								showDuration: 120, //动画中轨迹点高亮的持续时间
								playRate: 1000 // 动画播放倍速
							})
							.addTo(that.Map)
							.setData([{
								path: that.path
							}])
						/*author:孙玉斐
						data:2021/7/15
						引用折线构造函数，定义折线样式*/
						that.polylineLayer = new TMap.MultiPolyline({
							id: 'polyline-layer', //图层唯一标识
							map: that.Map, //设置折线图层显示到哪个地图实例中
							//折线样式定义
							styles: {
								'style_red': new TMap.PolylineStyle({
									'color': '#CC0000', //线填充色
									'width': 6, //折线宽度
									'lineCap': 'round' //线端头方式
								}),
								'style_green': new TMap.PolylineStyle({
									'color': '#00FF00', //线填充色
									'width': 6, //折线宽度
									'lineCap': 'round' //线端头方式
								})
							}
						});
						that.otherpolylineLayer = new TMap.MultiPolyline({
							id: 'otherpolyline-layer', //图层唯一标识
							map: that.Map, //设置折线图层显示到哪个地图实例中
							//折线样式定义
							styles: {
								'style_red': new TMap.PolylineStyle({
									'color': '#CC0000', //线填充色
									'width': 6, //折线宽度
									'lineCap': 'round' //线端头方式
								}),
								'style_green': new TMap.PolylineStyle({
									'color': '#00FF00', //线填充色
									'width': 6, //折线宽度
									'lineCap': 'round' //线端头方式
								})
							}
						});

						var polyArray = new Array();
						var i = 0;
						for (var item in that.tril) {
							polyArray[i] = [that.tril[i].lat, that.tril[i].lng],
								i = i + 1
						}
						//转为LatLng数组
						polyArray = polyArray.map(p => {
							return new TMap.LatLng(p[0], p[1]);
						});
						that.polylineLayer.add({ //新的折线添加到图层中
							'id': 'pl_1',
							'styleId': 'style_red',
							'paths': polyArray
						})
					});

				//实现地图上标记和点标记显示信息窗口的功能
				that.markerLayer = new TMap.MultiMarker({
					map: that.Map, //指定地图容器
					//标记样式自定义
					styles: {
						"myStyle": new TMap.MarkerStyle({
							"width": 25,
							"height": 35,
							"src": "https://mapapi.qq.com/web/lbs/javascriptGL/demo/img/markerDefault.png",
							"anchor": {
								x: 16,
								y: 32
							}
						})
					},
				});
				that.othermarkerLayer = new TMap.MultiMarker({
					map: that.Map, //指定地图容器
					//标记样式自定义
					styles: {
						"myStyle": new TMap.MarkerStyle({
							"width": 25,
							"height": 35,
							// "src": "https://mapapi.qq.com/web/lbs/javascriptGL/demo/img/markerDefault.png",
							"anchor": {
								x: 16,
								y: 32
							}
						})
					},
				});
				//初始化信息窗口
				var infoWindow = new TMap.InfoWindow({
					map: that.Map,
					position: new TMap.LatLng(30.5263, 114.348),
					offset: {
						x: 0,
						y: -32
					},
					enableCustome: true,
				});
				infoWindow.close();

				//标记点击事件，显示区域位置信息
				var clickHandler = function(evt) {
					infoWindow.open();
					infoWindow.setPosition(evt.geometry.position);
					that.nowname = evt.geometry.properties.name
					that.nowidenty = evt.geometry.properties.identy
					console.log(evt)
					that.$axios
						.post('https://120.25.76.71/api/content', {
							url: "https://apis.map.qq.com/ws/geocoder/v1/?location=" + evt.geometry.position.lat +
								"," + evt.geometry.position.lng + "&" + "key=EHABZ-5AFLU-T5XV6-BHFBR-JRKTZ-2UFJ6"
						})
						.then(res => {
							// 遍历marked的数组来加点
							console.log(res.data)
							infoWindow.setContent('<div class="content"><div class="divbox">' + evt.geometry
								.properties.name +
								'</div><div calss="divtextbox">' + res
								.data + '<br>' + evt.geometry.properties.Time +
								'</div></div>');
						})
				}
				that.markerLayer.on("click", clickHandler)
				that.othermarkerLayer.on("click", clickHandler)
				console.log(that.Map)
				console.log(window)
			},

			/* author:李浩杨
			date:7.20
			点击按钮对选中目标进行溯源*/
			change() {
				this.$confirm('是否确认要进行回溯?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					console.log(this.nowname)
					console.log(this.nowidenty)
					let that = this
					that.Map.destroy()
					that.sign(this.nowname, this.nowidenty, that.temp["time"])
					that.judge = true
					that.temp["name"] = this.nowname;
					that.temp["identy"] = this.nowidenty
					that.marked = []
					that.path = []
					that.tril = []
					that.name = []
					that.identy = []
				}).catch(() => {});
			},
		},
		/* author:孙玉斐
		date:7.17
		页面加载完成时进行初始化地图并且绘制患者路径与地区信息*/
		mounted() {
			this.initMap();
			// this.sign(this.$route.query.name, this.$route.query.identy, this.$route.query.time)
			if (this.$route.query.display) {
				this.Map.destroy()
				this.sign(this.$route.query.name, this.$route.query.identy, this.$route.query.time)
			}
		},
	}
</script>

<style>
	.box {
		width: 100%;
		height: 100%;
	}

	.box #container {
		/*地图(容器)显示大小*/
		/*设置为全屏，位于最下层*/
		width: 100%;
		height: 100%;
		z-index: 1;
	}

	.box .info1 {
		position: absolute;
		left: 200px;
		top: 60px;
		z-index: 999;
	}

	.box .info1 .button1 {
		width: 90px;
		height: 45px;
		font-size: 15px;
		position: absolute;
		left: 20px;
		top: 10px;
		border-radius: 30px;
		background-image: linear-gradient(120deg, #e0c3fc 0%, #8ec5fc 100%);
		z-index: 1000;
	}

	.box .info2 {
		position: absolute;
		left: 310px;
		top: 60px;
		z-index: 999;
	}

	.box .info2 .button2 {
		width: 90px;
		height: 45px;
		font-size: 15px;
		position: absolute;
		left: 20px;
		top: 10px;
		border-radius: 30px;
		background-image: linear-gradient(to top, #9795f0 0%, #fbc8d4 100%);
		z-index: 1000;
	}

	.box .word1 {
		width: 160px;
		height: 20px;
		position: absolute;
		right: 150px;
		top: 90px;
		z-index: 2;
		display: flex;
		align-items: center;
	}

	.box .word1 .line1 {
		width: 50px;
		height: 5px;
		background-color: red;
	}

	.box .word2 {
		width: 160px;
		height: 20px;
		position: absolute;
		right: 150px;
		top: 150px;
		z-index: 2;
		display: flex;
		align-items: center;
	}

	.box .word2 .line2 {
		width: 60px;
		height: 5px;
		background-color: greenyellow;
	}

	.box .word3 {
		width: 160px;
		height: 20px;
		position: absolute;
		right: 150px;
		top: 210px;
		z-index: 2;
		display: flex;
		align-items: center;
	}

	.box .word3 .circle1 {
		width: 20px;
		height: 10px;
		border-radius: 50%;
		background-color: red;
	}

	.box .word4 {
		width: 180px;
		height: 20px;
		position: absolute;
		right: 130px;
		top: 270px;
		z-index: 2;
		display: flex;
		align-items: center;
	}

	.box .word4 .circle2 {
		width: 20px;
		height: 10px;
		border-radius: 50%;
		background-color: #00BFFF;
	}

	.box .word5 {
		width: 160px;
		height: 20px;
		position: absolute;
		right: 150px;
