import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../components/Login.vue'
import Register from '../components/Register.vue'
import Home from '../components/Home.vue'
import Map from '../components/Map.vue'
import Search from '../components/user/Search.vue'
import Enter from '../components/user/Enter.vue'
import Page from "../components/user/Page.vue";
import Message from "../components/user/Message.vue"

Vue.use(VueRouter)

const routes = [
  {path:'/',redirect:'/login'},
  {path:'/login',component:Login},
  {path:'/register',component:Register},
  {path:'/home', component:Home,redirect:'/map',
	children:[
		{path:'/map',name:'Map',component:Map},
		{path:'/search',component:Search},
		{path:'/enter',component:Enter},
	]},
	{path: "/page",name: "Page",component: Page,},
	{path: "/message",name: "Message",component: () => import("../components/user/Message.vue"),},
]

const router = new VueRouter({
  routes
})

export default router
